import * as React from 'react';

class Menu extends React.Component {
    changeNick(){
        let nick = document.querySelector(".nick").value;
        if(nick.length<1){
            nick = "Jakiś noName";
        }
        this.props.setNick(nick);
    }
  render() {
    return (
        <div className="menu">
            <div className="content">
                <h1>{this.props.tekst}</h1>
                <input type="Text" maxLength="10" className="nick" placeholder="Enter you'r name"/>
                <input type="button" className="start" value="Start" onClick={this.changeNick.bind(this)}/>
            </div>
        </div>
    );
  }
}

export default Menu;


