import React, { Component } from 'react';
import { galaxy } from '../js/galaxy.js';


class Game extends Component {
    componentDidMount(){
        galaxy(150)
    }
  render() {
        return ( 
            <div>
            <canvas id="stars"></canvas>
            </div>
        );
  }
}

export default Game;


