import React, { Component } from 'react';
import * as firebase from 'firebase';
import '../css/App.css';
import Menu from './menu';
import Game from './game';
import Bar from './bar';



class App extends Component {
  constructor(){
    super();

    this.title = firebase.database().ref().child('hello');
    
    this.state = {
      nick: "Bezimienny",
      score: 0,
      run:false,
      welcomer:"Connecting...",
    };
  }
  setNick(nickname){
    this.setState({nick:nickname,run:true});    
  }
  componentDidMount(){
    this.title.on('value', snap => {
      this.setState({
        welcomer:snap.val()
      });
    });
  }
  
  render() {
    if(this.state.run){
    return (
      <div className="App">
      <Bar nickName={this.state.nick} score={this.state.score}/>
      <Game/>
      </div>
    );
    }
    return (
      <div className="App">
        <Menu setNick={this.setNick.bind(this)} tekst={this.state.welcomer}/>
      </div>
    );
  }
}

export default App;



